  watch: {
    contactData: {
      deep: true,
      handler(newVal, oldVal) {
        this.selectedUser = newVal;
      },
    },
    newType: {
      deep: true,
      handler(newVal, oldVal) {
        let self = this;
        // newType: {
        //   name: "",
        //   price: "",
        //   icon: "",
        //   packageOpen: false,
        //   package: 5,
        //   duration: "",
        // },

        if (
          !self.$tools.isNull(self.newType.name) &&
          !self.$tools.isNull(self.newType.price) &&
          !self.$tools.isNull(self.newType.duration) &&
          !self.$tools.isNull(self.newType.price)
        ) {
          self.newTypeIsEmpty = false;
        } else {
