  },
  methods: {
    async addLessonType() {
      let self = this;
      let now = parseInt(new Date().getTime() / 1000).toString();

      // newType: {
      //   name: "",
      //   price: "",
      //   icon: "",
      //   packageOpen: false,
      //   package: 5,
      //   duration: "",
      // },

      let content = {
        id: commons.generateId(),
        teacherId: self.lessonData.teacherId,
        instrumentId: self.lessonData.instrumentId,
        timeLength: self.newType.duration,
        price: self.newType.price,
        type: 1,
        name: self.newType.name,
        deleted: false,
        package: self.newType.packageOpen ? self.newType.package : 0,
        createTime: now,
        updateTime: now,
      };

      let success = await teacherService.addLessonType(content);

      self.isButtonLoadingComplete = true;

      if (success) {
        // 添加成功
        self.$notify({
          message: "Add Success!",
          type: "success",
        });

        self.isLoading = true;
        await self.initLessonType();

        self.showAddLessonType = false;
        self.showLessonType = true;
      } else {
        // 添加失败
        self.$notify({
          message: "Add failed, try it again",
          type: "error",
        });
      }

      self.isButtonLoadingComplete = false;
    },
    async updateLessonConfig() {
      let self = this;
      let update = self.$firebase
        .functions()
        .httpsCallable("deleteLessonsAfterSpecificTimeBasedOnConfigId");

      let weekdays = [];
      self.week.some((item) => {
        if (item.selected) {
          weekdays.push(item.value);
        }
      });

      let config = {
        time: parseInt(new Date(self.startsOn).getTime() / 1000),
        configId: self.lessonData.originData.lessonScheduleConfigId,
        isUpdate: true,
        studentId: self.lessonData.studentId,
        teacherId: self.lessonData.teacherId,
        newScheduleData: JSON.stringify({
          teacherId: self.lessonData.teacherId,
          studentId: self.lessonData.studentId,
          lessonTypeId: self.currentLessonType.id,
          startDateTime: parseInt(new Date(self.startsOn).getTime() / 1000),
          repeatType: self.recurrenceOpen ? (self.weekly ? 1 : 2) : 0,
          repeatTypeWeekDay: weekdays,
          endType: self.endsOpen ? (self.ends.radio === "endsOn1" ? 1 : 2) : 0,
          endDate: parseInt(
            new Date(
              self.$tools.formatYYMMDD(new Date(self.ends.date).getTime())
            ).getTime() / 1000
          ),
          lessonStatus: 1,
          endCount: self.ends.count,
          delete: false,
        }),
        // newScheduleData: ''
      };

      console.log(config);

      update(config)
        .then(async (result) => {
          console.log(result.data.data);
          self.isButtonLoadingComplete = true;
          self.$notify({
            message: "Update Success!",
            type: "success",
          });

          self.$emit("update");
          self.modalClose();
        })
        .catch((err) => {
          console.log(err);
          self.$notify({
            message: "Update failed, try it again!",
            type: "error",
          });
          self.isButtonLoadingComplete = true;
        });

      self.isButtonLoadingComplete = false;
    },
    async addLessonConfig() {
      let self = this;
      let now = parseInt(new Date().getTime() / 1000);
      let add = self.$firebase
        .functions()
        .httpsCallable("setLessonScheduleConfig");

      let weekdays = [];
      self.week.some((item) => {
        if (item.selected) {
          weekdays.push(item.value);
        }
      });

      let data = {
        invitedStatusIsinviteAndResend: self.selectedUser.invitedStatus === 0,
        configs: [
          {
            lessonType: self.currentLessonType,
            lessonScheduleConfig: {
              teacherId: self.selectedUser.teacherId,
              studentId: self.selectedUser.studentId,
              lessonTypeId: self.currentLessonType.id,
              startDateTime: parseInt(new Date(self.startsOn).getTime() / 1000),
              repeatType: self.recurrenceOpen ? (self.weekly ? 1 : 2) : 0,
              repeatTypeWeekDay: weekdays,
              lessonStatus: 1,
              endType: self.endsOpen
                ? self.ends.radio === "endsOn1"
                  ? 1
                  : 2
                : 0,
              endDate: parseInt(
                new Date(
                  self.$tools.formatYYMMDD(new Date(self.ends.date).getTime())
                ).getTime() / 1000
              ),
              endCount: self.ends.count,
              delete: false,
              createTime: now,
              updateTime: now,
            },
          },
        ],
      };

      console.log(data);
      console.log(JSON.stringify(data));

      add(data)
        .then(async (result) => {
          console.log(result.data.data);
          self.isButtonLoadingComplete = true;
          self.$notify({
            message: "Add Success!",
            type: "success",
          });

          this.$emit("add");
          this.modalClose();
        })
        .catch((err) => {
          console.log(err);
          self.$notify({
            message: "Add failed, try it again!",
            type: "error",
          });
          self.isButtonLoadingComplete = true;
        });

      self.isButtonLoadingComplete = false;
    },
    selectLessonType(item, index) {
      let self = this;
      self.lessonTypes.some((lessonType) => {
        lessonType.selected = false;
      });
      item.selected = true;
      self.currentLessonType = item;
    },
    selectWeekday(item) {
      let self = this;
    },
    getAvailableTime(data) {
      console.log(data);
      this.availableTime = data.availableTime;
      this.startsOn = data.selectedTimeStr;
    },
    selectUsers(data) {
      let self = this;

      if (self.selectUser) {
        if (self.$tools.isNull(data)) {
          self.showSelectUser = true;
          self.showBasic = false;
        } else {
          self.selectedUser = data;
          self.showSelectUser = false;
          self.showBasic = true;
        }
      }
    },
    close() {
      this.$emit("close");
      this.modalClose();
    },
    modalClose() {
      document.getElementsByTagName("body")[0].classList.remove("modal-open");
    },
    async init() {
      let self = this;
      setTimeout(() => {
        self.show = true;
      }, 50);

      if (!self.$tools.isNull(self.name)) {
        self.fullName = self.name;
      }

      if (!self.$tools.isNull(self.data)) {
        self.fullName = self.data.name;
        self.phone = self.data.phone;
        self.email = self.data.email;
      }

      if (!self.$tools.isNull(self.contactData)) {
        self.selectedUser = self.contactData;
      }

      await self.initStudents();

      await self.initLessonType();
    },
    async initStudents() {
      this.users = await teacherService.listStudentsOfTeacher(
        this.lessonData.teacherId
      );
    },
    async initLessonType() {
      let self = this;
      if (!self.$tools.isNull(self.lessonData)) {
        self.lessonTypes = await query.getLessonTypesByTeacherId(
          self.lessonData.teacherId
        );
        self.lessonTypes.sort((a, b) => {
          return parseInt(a.createTime) - parseInt(b.createTime)
        })
        if (self.lessonData.originData) {
          for (let i = 0; i < self.lessonTypes.length; i++) {
            let item = self.lessonTypes[i];
            console.log("lesson type", item);
            if (item.id === self.lessonData.originData.lessonTypeId) {
              item.selected = true;
              self.currentLessonType = item;
              console.log("currentType: ", self.currentLessonType);
              if (self.currentLessonType.package > 0) {
                self.currentLessonType.leftCount = await query.getPackageLessonLeftCountById(
                  self.currentLessonType.id,
                  self.lessonData.originData.lessonScheduleConfigId
                );
              }
              break;
            }
          }

          // lesson config -> start on
          let config = await query.getLessonScheduleConfigById(
            self.lessonData.originData.lessonScheduleConfigId
          );
          if (!self.$tools.isNull(config)) {
            let startDateTime = config.startDateTime * 1000;
            self.startsOn =
              self.$tools.formatYYMMDD(startDateTime) +
              " " +
              self.$tools.formatHHMM(startDateTime);
          }
          // console.log(config);

          // recurrence
          self.recurrenceOpen = config.repeatType !== 0;
          self.weekly = config.repeatType === 1;
          self.resetWeek();
          for (let i = 0; i < config.repeatTypeWeekDay.length; i++) {
            let day = config.repeatTypeWeekDay[i];
            self.week.some((item) => {
              if (day === item.value) {
                item.selected = true;
              }
            });
          }

          // ends
          self.endsOpen = config.endType !== 0;
          self.ends = {
            radio: "endsOn" + config.endType,
            date: self.$tools.formatYYMMDD(config.endDate * 1000),
            count: config.endCount,
          };
        }
      }
      self.isLoading = false;
    },
    resetWeek() {
      this.week.some((item) => {
        item.selected = false;
      });
    },
    selectDuration(item) {
      let self = this;
      self.durations.some((time) => {
        time.selected = false;
      });
      item.selected = true;
      self.newType.duration = item.length;
    },
  },
};
